.container {
    margin: 10px;
    display: flex;
    flex-direction: column;
}

.item {
    width: 230px;
    display: flex;
    justify-content: space-between;
}

.buttonContainer {
    margin: 10px;
    display: flex;
    width: 250px;
    justify-content: space-between;
}

.buttonContainer button{
    min-width: 45px;
    max-width: 70px;
    height: 20px;
}
